n        .then((response) => {\n          if (!response || !response.data || !response.data.data) {\n            throw new Error();\n          }\n          page.value = response?.data?.data;\n          \n          setTitle(page.value.title);\n        })\n        .catch(() => {\n          router.push({\n            name: 'NotFound',\n            params: {\n              pathMatch: route?.path.substring(1)\n                .split('/'),\n            },\n            query: route?.query,\n            hash: route?.hash,\n          });\n        })\n        .finally(() => {\n          loading.value = false;\n        });\n    }\n  },\n  {\n    immediate: true,\n  },\n);\n\n</script>\n\n<style scoped>\n.prose p:first-child {\n  margin-top: 0;\n}\n\n</style>\n"],"sourceRoot":""}]);
